.about-section {
	.conversational-form {
		font-family: $customfont-6;
		font-weight: 300;
		font-size: 13px;
		@media screen and (min-width: $desktop-small) {
			width: 550px;
		}
	}
	.conversational-form cf-chat scrollable::-webkit-scrollbar {
		width: 0px;
	}
	.conversational-form cf-chat-response {
		width: auto;
		max-width: 80%;
		padding-top: 0px;
		min-width: 200px;
	}
	@media only screen and (max-width: 769px) {
		.conversational-form cf-chat-response {
			max-width: 83%;
		}
	}
	.conversational-form cf-chat-response text {
		font-weight: 900;
		min-height: 62px;
		font-size: 13px;
	}
	.conversational-form cf-chat-response thumb {
		height: 62px;
		width: 62px;
		background-repeat: no-repeat;
	}
	.conversational-form cf-chat-response.robot thumb {
		background-image: url('#{$images-url}/fire.svg') !important;
		background-size: 32px 32px;
	}
	.conversational-form cf-chat-response.user thumb {
		background-image: url('#{$images-url}/alien.svg') !important;
		background-size: 32px 44px;
	}
	.conversational-form cf-chat-response thumb > p {
		background-color: #ebebeb;
	}
	.conversational-form cf-chat-response:not(:last-of-type) {
		margin-bottom: 10px;
	}
	.conversational-form cf-chat-response.robot {
		padding-left: 68px;
	}
	.conversational-form cf-chat-response.robot text {
		color: #222;
	}
	.conversational-form cf-chat-response.robot text > p {
		width: 100%;
		margin: 6px 0 0;
		padding: 16px 24px;
		border-radius: 4px 20px 20px 20px;
		background: $altcolor;
		color: $lightcolor;
	}
	.conversational-form cf-chat-response.user text > p {
		width: 100%;
		margin: 6px 0 0;
		padding: 16px 24px;
		border-radius: 20px 4px 20px 20px;
		background: #F2F3F6;
	}
	.conversational-form cf-chat-response.user {
		padding-right: 68px;
	}
	.conversational-form cf-chat-response.user text {
		color: $altcolor;
	}
	.conversational-form cf-input input,
	.conversational-form cf-input textarea {
		border-radius: 40px;
		min-height: 57px;
		padding: 19px 60px 19px 30px;
		height: 56px;
		font-size: 14px;
		font-weight: 300;
		font-family: $customfont-6;
		color: $customfont-6;
		background: #F2F3F6;
		border-color: transparent;
	}
	@media only screen and (min-width: 769px) {
		.conversational-form cf-input input,
		.conversational-form cf-input textarea {
			font-size: 16px;
		}
	}
	.conversational-form cf-input input:not(:focus),
	.conversational-form cf-input textarea:not(:focus) {
		box-shadow: 0 0 1px rgba(58,58,58,0.5);
	}
	.conversational-form cf-input-button {
		background: #222;
		height: 42px;
		width: 42px;
		border: none;
	}
	.conversational-form cf-input-button.microphone-interface {
		background: #fff;
	}
	.conversational-form cf-input-button .cf-icon-progress {
		background-image: url("data:image/svg+xml;charset=utf-8;base64,");
		background-size: 18px 19px;
	}
	.conversational-form cf-input-button.loading:after {
		border: 2px solid #1ed97b;
	}
	.conversational-form cf-input-button .cf-icon-audio {
		background-image: url("data:image/png;charset=utf-8;base64,");
		background-size: 12px 19px;
		mix-blend-mode: difference;
	}
	.conversational-form cf-input-button cf-icon-audio-eq {
		border-color: #222;
	}
	.conversational-form .cf-button {
		color: #222;
		border-color: #ddd;
		transition: opacity 0.75s cubic-bezier(0.215, 0.61, 0.355, 1), border-color 0.75s cubic-bezier(0.215, 0.61, 0.355, 1), color 0.75s cubic-bezier(0.215, 0.61, 0.355, 1);
		background: none;
		font-weight: 900;
		line-height: 1.2;
	}
	.conversational-form .cf-button > div {
		padding: 14px 24px;
	}
	.conversational-form .cf-button cf-radio {
		background: rgba(34,34,34,0.2);
	}
	.conversational-form .cf-button.highlight,
	.conversational-form .cf-button:focus,
	.conversational-form .cf-button:hover,
	.conversational-form .cf-button[checked="checked"],
	.conversational-form .cf-button.selected {
		border-color: #6f6f6f;
		color: #595959;
	}
	.conversational-form .cf-button.highlight cf-radio,
	.conversational-form .cf-button:focus cf-radio,
	.conversational-form .cf-button:hover cf-radio,
	.conversational-form .cf-button[checked="checked"] cf-radio,
	.conversational-form .cf-button.selected cf-radio {
		background: #222;
	}
	.conversational-form cf-list-button:after {
		background-image: none;
		background: #000;
		width: 5px;
		height: 5px;
		border-radius: 50%;
	}
}